body {
  overflow-y: hidden;
}

.GameBase {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f5f5;
}

table {
  border-collapse: collapse;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

td {
  width: 100px;
  height: 100px;
  text-align: center;
  vertical-align: middle;
  font-size: 48px;
  font-weight: bold;
  color: #333;
  background-color: #fff;
  border: 2px solid #ccc;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  border-radius: 8px;
  position: relative;
}

@keyframes win {
  0% {
    transform: scale(1) translateY(-100%);
    opacity: 0;
  }

  50% {
    transform: scale(1.1) translateY(0);
    opacity: 1;
  }

  100% {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}



/*** Winners ................ */



/* ! winner is X .......... */

td.x.winner {
  background: #f44336;
  border-color: white;
  color: white !important;
  animation-name: win;
  animation-duration: 0.4s;
  animation-timing-function: ease-in-out;
}

td.x.winner:active {
  background: #f44336;
  border-color: #f44336;
  color: white !important;
}

td.x.winner:hover {
